      p Estas estrategias deben aplicarse desde la sensibilidad cultural y el respeto a las comunidades anfitrionas, estableciendo acuerdos previos sobre los contenidos, mensajes e intenciones comunicativas. En la interpretación del patrimonio étnico, se deben seguir protocolos de autenticidad que preserven la dignidad de las prácticas tradicionales, evitando su mercantilización o banalización turística.
      .col-md-11
        h3 Uso de recursos visuales, gráficos y simbólicos
        p El guía o intérprete del patrimonio puede complementar su relato mediante recursos visuales y gráficos como mapas, fotografías, ilustraciones, maquetas o símbolos locales. Estos elementos favorecen la comprensión del mensaje, fortalecen el valor educativo de la experiencia y actúan como puentes interculturales entre las comunidades anfitrionas y las personas visitantes. El uso de recursos visuales, gráficos y simbólicos no tiene solo una función estética o informativa, sino también pedagógica y cultural: facilita el aprendizaje, promueve el diálogo de saberes y transmite la cosmovisión de cada grupo étnico. A continuación, se identifican los principales tipos de recursos aplicados en el turismo étnico y sostenible:

